// @mui
import { useEffect, useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import DataTable from 'react-data-table-component';
import { InputAdornment, TextField, Button, IconButton, Box, Tabs, Tab, Stack, Chip, Card, CardContent } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import { decryptToken, formatObj } from 'src/utils/common';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import CashImage from 'src/assets/images/universal_currency_alt.png';
import CreditCardImage from 'src/assets/images/credit-card 1.png';
import UPIImage from 'src/assets/images/operation 1.png';

// ----------------------------------------------------------------------

export default function PaymentsView() {
  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [type, setType] = useState(['VENDOR']);

  const [data, setData] = useState({
    balanceSummary: {},
    typeSummaryDifferedByPaymentModeTypes: [],
    payments: []
  });

  const filteredRecords = data.payments.filter((filteredData) => filteredData);

  const fetchData = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.expenditure.paymentSummary, { type }, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsLoading(false);
        setData(response.data.data);
      };

    } catch (err) {
      enqueueSnackbar('Failed to fetch records!', { variant: 'error' });
      setIsLoading(false);
    }
  };

  useEffect(() => {
    setIsLoading(true);
    fetchData();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type]);

  const columns = [
    {
      name: 'Invoice Number',
      selector: (row) => row.invoiceNumber,
      sortable: true,
    },
    {
      name: 'Amount Paid',
      selector: (row) => row.amount,
      sortable: true,
    },
    {
      name: 'Payment Mode',
      selector: (row) => row.paymentMode,
      sortable: true,
    },
    {
      name: 'Payment Status',
      selector: (row) => row.paymentStatus,
      sortable: true,
    },
    {
      name: 'Party Type',
      selector: (row) => row.partyType,
      sortable: true,
    },
    {
      name: 'Payment Date',
      selector: (row) => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
      sortable: true,
    },
    {
      name: 'Actions',

    },
  ];
  const [value, setValue] = useState('one');

  const handleChange = (event, newValue) => {
    setValue(newValue);
    if (newValue === 'one') {
      setType(['VENDOR']);
    } else {
      setType(['INCOME', 'EXPENSE']);
    }
  };

  console.log(data.balanceSummary)
  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Typography variant="h4"> Payments </Typography>

      <Box sx={{ width: '100%' }}>
        <Tabs
          value={value}
          onChange={handleChange}
          textColor="secondary"
          indicatorColor="secondary"
          aria-label="secondary tabs example"
        >
          <Tab value="one" label="Vendors" />
          <Tab value="two" label="Expense/Income" />
        </Tabs>
      </Box>


      <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginTop: '10px', gap: "10px", padding: '10px', backgroundColor: '#E6F2FF', borderRadius: '10px' }}>


        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
            <img src={CashImage} alt='currency_Image' />
            <Typography variant="subtitle1" color='#E11900'>CASH</Typography>
          </div>
          <div  >
            <Typography sx={{ fontSize: 24 }}>
              {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'CASH').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'CASH').totalAmount) : formatObj.format(0)}
            </Typography>

          </div>
        </div>

        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
            <img src={UPIImage} alt='upi_Image' />
            <Typography variant="subtitle1" color='#008D2F'>UPI</Typography>
          </div>
          <div  >
            <Typography sx={{ fontSize: 24 }}>
              {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'UPI').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'UPI').totalAmount) : formatObj.format(0)}
            </Typography>
          </div>
        </div>

        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
            <img src={CreditCardImage} alt='card_Image' />
            <Typography variant="subtitle1" color='#E11900'>CARD</Typography>
          </div>
          <div  >
            <Typography sx={{ fontSize: 24 }}>
              {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'CARD').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'CARD').totalAmount) : formatObj.format(0)}
            </Typography>
          </div>
        </div>



      </div>



      <Stack direction="row" spacing={1} sx={{ mt: 2 }}>
        <Chip
          label={`Net Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.totalAmount) : formatObj.format(0)}`}
          variant="outlined"
        />
        <Chip
          label={`Received Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.receivedBalance) : formatObj.format(0)}`}
          variant="outlined"
        />
        <Chip
          label={`Paid Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.paidBalance) : formatObj.format(0)}`}
          variant="outlined"
        />
      </Stack>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '10px' }}>
        <TextField
          size="small"
          onChange={(e) => setSearchQuery(e.target.value)}
          type="text"
          placeholder="Search"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
      </div>
      <div style={{ marginTop: "20px" }}>
        <DataTable
          columns={columns}
          data={filteredRecords}
          pagination
          progressPending={isLoading}
        />
      </div>

    </Container>
  );
}
